import request from '@/plugin/axios';
export default {
  name: 'task-list',
  data: function data() {
    return {
      filename: __filename,
      loading: true,
      query: {
        title: '',
        isFinish: null
      },
      pagination: {
        currentPage: 1,
        pageSize: 20,
        pageSizes: [10, 20, 30, 50, 100],
        total: 0
      },
      tableData: [],
      options: [{
        value: true,
        label: '已完成'
      }, {
        value: false,
        label: '未完成'
      }]
    };
  },
  created: function created() {
    this.getTableData();
  },
  watch: {
    'pagination.currentPage': function paginationCurrentPage(val) {
      this.getTableData();
    },
    'pagination.pageSize': function paginationPageSize(val) {
      this.getTableData();
    }
  },
  methods: {
    formatterFinish: function formatterFinish(row, column, cellValue) {
      if (row.finish === true) {
        return '已完成';
      } else {
        return '未完成';
      }
    },
    finish: function finish(row) {
      var _this = this;

      var params = {
        id: row.id
      };
      request.post('/task/finish', params).then(function (res) {
        if (res.code === 0) {
          _this.$message.success('任务完成');

          _this.getTableData();
        }
      });
    },
    search: function search() {
      this.pagination.currentPage = 1;
      this.getTableData();
    },
    addTask: function addTask() {// this.$router.push({name: 'user-edit'})
    },
    reset: function reset(formName) {
      this.$refs[formName].resetFields();
      this.getTableData();
    },
    handleSizeChange: function handleSizeChange(val) {
      this.pagination.pageSize = val;
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.pagination.currentPage = val;
    },
    getTableData: function getTableData() {
      var _this2 = this;

      this.loading = true;
      var page = this.pagination;
      var params = {
        'index': page.currentPage,
        'size': page.pageSize
      };
      var filters = {
        'title': this.query.title,
        'finish': this.query.isFinish
      };
      params.filters = filters;
      request.get('/task/getMyTask', {
        params: params
      }).then(function (res) {
        _this2.pagination.total = res.data.count;

        if ((_this2.pagination.currentPage - 1) * _this2.pagination.pageSize >= _this2.pagination.total) {
          _this2.pagination.currentPage--;
          return;
        }

        _this2.tableData = res.data.list;
        _this2.loading = false;
      }).catch(function () {
        _this2.loading = false;
      });
    }
  }
};